@font-face {
  font-family: 'Noto Sans Thai';
  src: url(../src/fonts/NotoSansThaiUI-Light.ttf);
  font-weight: 300;
}

@font-face {
  font-family: 'Noto Sans Thai';
  src: url(../src/fonts/NotoSansThaiUI-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: 'Noto Sans Thai';
  src: url(../src/fonts/NotoSansThaiUI-Medium.ttf);
  font-weight: 500;
}

@font-face {
  font-family: 'Noto Sans Thai';
  src: url(../src/fonts/NotoSansThaiUI-SemiBold.ttf);
  font-weight: 600;
}

@font-face {
  font-family: 'Noto';
  src: url(../src/fonts/NotoSans-Regular.ttf);
}

body {
  font-family: 'Noto Sans Thai', 'Noto';
}

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}
